.log{
    width: 1050px;
    margin: 50px auto;
    text-align: center;
}
.log > input{
    width: 312px;
    height: 50px;
    font-weight: 400;
    padding: 0px 11px 1px 15px;
    border: 1px solid #ccc;
    border-radius: 4px;
}
.log > h3{
    margin-top: 85px;
}
.id{
    margin-top: 30px;
}
.pw{
    margin-top: 10px;
}
.log > div{
    margin: 10px auto 30px;
    width: 329px;
}
.log > div > p{
    float: right;
    color: #333;
    font-size: 13px;
}
.log > div > .bar{
    margin: 0px 6px;
}
.log > button{
    width: 340px;
    height: 54px;
    border-radius: 3px;
}
.signin{
    font-weight: bolder;
    font-size: 14px;
    margin-top: 28px;
    border: 1px solid #8a2be2;
    background-color: #8a2be2;
    color: #fff;
}
.signup{
    font-weight: bolder;
    font-size: 14px;
    margin-top: 10px;
    border: 1px solid #8a2be2;
    background-color: #fff;
    color: #8a2be2;
}