@charset "utf-8";
*{margin: 0px; padding: 0px;}
ul, li, ol{list-style: none;}
a{text-decoration: none;}

body{
    font-family: "맑은고딕", Arial, sans-serif;

}
header{
    width: 100%;
    height: 156px;
    box-shadow: 0px 0px 3px #333;
}
.h_inner{
    width: 1050px;
    height: 156px;
    margin: 0px auto;
    text-align: center;
}
.h_inner ul{
    margin: 0px auto;
    padding: 0px;
    display: flex;
    justify-content: space-evenly;
}
.h_inner .h_top{
    width: 1050px;
    height: 32px;
}
.h_inner .h_top_nav{
    width: 200px;
    height: 32px;
    line-height: 32px;
    float: right;
    font-size: 12px;
}
.h_inner .h_top_nav > li > a{
    color: #333;
}
.h_inner .h_top_nav > li:first-child > a{
    color: rgb(95, 0, 128);
}
.h_inner .h_top_nav > li:nth-child(2n){
    color: #777;
    margin: 0px 12px;
}

.h_inner .h_mid{
    padding-top: 10px;
    margin-bottom: 10px;
    height: 50px;
    position: relative;
}
.h_inner .h_mid > a > img,
.h_inner .h_mid > h1{
    float: left;
}
.h_inner .h_mid > h1 > a{
    margin-left: 20px;
    line-height: 42px;
    font-size: 18px;
}
.h_inner .h_mid > .mar > a{
    color: rgb(95, 0, 128);
}
.h_inner .h_mid > .but > a{
    color: #b5b5b5;
}
.h_inner .h_mid > a > img{
    width: 82; height: 42px;
}
.h_inner .h_search{
    width: 380px; height: 48px;
    position: absolute;
    float: left;
    left: 325px;
    padding-left: 14px;
    border: 1px solid rgb(95, 0, 128);
    border-radius: 6px;
    background-color: #fff;
}
.h_inner .h_search > input{
    width: 300px;
    font-size: 16px;
    float: left;
    height: 48px;
    border: none;
}
.h_inner .h_search > svg{
    width: 28px; height: 28px;
    font-size: 28px;
    float: right;
    margin: 10px;
}
.h_inner .h_mid .h_mid_icon{
    width: 140px;
    height: 36px;
    float: right;
}
.h_inner .h_mid .h_mid_icon > li:first-child > svg{
    font-size: 32px;
    margin-right: 20px;
}
.h_inner .h_mid .h_mid_icon > li:nth-child(2) > svg{
    font-size: 34px;
}
.h_inner .h_mid .h_mid_icon > li:last-child > svg{
    margin-left: 20px;
    font-size: 35px;
}
.h_bot{
    height: 52px;
    line-height: 52px;
    font-weight: bold;
}
.h_bot > .s{
  float: right;
  margin: 10px 0px;
  width: 170px;
  height: 30px;text-align: center;
  line-height: 30px;
  border: 1px solid #777;
  border-radius: 15px;
}
.h_bot a span{
  color: rgb(95, 0, 128);
}
.h_bot > .k{
    float: left;
}
.h_bot > .k > a,
.h_bot > .s > a,
.h_bot > div .h_nav  > li > a{
    color: #333;
}
.h_bot div{
  display: block;
  float: left;
  left: 40px;
  width: 600px;
  height: 52px;
  padding-left: 140px;
}
.h_bot div > .h_nav{
  width: 600px;
  height: 52px;
}

main .slide_wrapper{
    width: 1900px;
    height: 370px;
    margin: 0px auto;
    position: relative;
    overflow: hidden;
}
main .slide_wrapper > ul{
    display: flex;
    width: calc(1900px*7);
    position: absolute;
    left: 0px; top: 0px;
    transition: 0.2s;
}
main .slide_wrapper > ul > li{
    width: 1900px;
    height: 370px;
}
main .slide_wrapper > ul > li > img{
    width: 100%; height: 100%;
}
.btn_group > input[type=button]{
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 50px;
  height: 50px;
  border: 1px solid rgba(204, 204, 204);
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.4);
  font-size: 40px;
}
.btn_group .prev{
  left: 10%;
}
.btn_group .next{
  right: 10%;
}
main .m_box{
    width: 1050px;
    margin: 0px auto;
}
main .m_box img{
    width: 100%;
}
main h3{
    margin: 80px auto 10px;
    text-align: center;
    font-size: 28px;
    font-weight: 500;
}
main .m_box h3 svg{
    width: 25px; height: 25px;
}
main .product_list{
    margin: 20px auto 50px;
    width: 1050px;
    display: flex;
    justify-content: space-evenly;
}
main .product_list > li{
    position: relative;
    width: 250px; height: 434px;
}
main .product_list > li > .name{
    display: block;
    height: 40px;
    margin-bottom: 10px;
    font-size: 16px;
    color: #333;
}
main .product_list > li > .per{
    float: left;
    margin-right: 10px;
    display: block;
    font-size: 18px;
    line-height: 30px;
    height: 30px;
    color: #e55;
    font-weight: bold;
}
main .product_list > li > .price{
    display: block;
    font-size: 18px;
    height: 30px;
    color: #333;
    font-weight: bold;
}
main .product_list > li > .nope{
    font-size: 16px;
    text-decoration: line-through;
    color: #bbb;
    font-weight: bold;
}
main .product_list > li > span{
    font-size: 28px;
    width: 40px; height: 40px;
    line-height: 40px;
    text-align: center;
    border-radius: 50%;
    position: absolute;
    right: 10px; bottom: 125px;
    background-color: rgba(221, 68, 68, 0.8);
    color: #fff;
}
main .product_list > li > div{
    margin-top: 0px;
    height: 320px;
    width: 100%;
    overflow: hidden;
    position: relative;
}
main .product_list > li > div > img{
    position: absolute;
    margin-top: 0px;
    height: 320px;
    width: 100%;
    transition: 0.4s;
}
main .product_list > li > div > img:hover{
    transform: scale(1.02);
}

footer{
    width: 100%;
    height: 450px;
    border-top: 1px solid #ccc;
}
footer > a{
    position: fixed;
    width: 50px; height: 50px;
    color: #fff;
    background: rgb(95, 0, 128);
    border: 1px solid rgb(95, 0, 128);
    border-radius: 50%;
    text-align: center;
    line-height: 50px;
    right: 10px; bottom: 20px;
    cursor: pointer;
    transition: 0.2s;
}
footer > a:hover{
    color: rgb(95, 0, 128);
    background: #fff;
    border: 1px solid rgb(95, 0, 128);
}
.f_inner{
    width: 1050px;
    height: 350px;
    padding-top: 40px;
    margin: 0px auto;
}
.f_ask{
    width: 50%;
    height: 350px;
    float: left;
}
.f_ask > h2{
    font-size: 20px;
}
.f_happy{
    width: 100%;
    height: 32px;
}
.f_ask .f_happy > h1{
    display: block;
    float: left;
    height: 32px;
    width: 150px;
    font-size: 28px;
}
.f_ask .f_happy > p{
    display: block;
    float: left;
    height: 32px;
    line-height: 40px;
    font-size: 16px;
}
.f_ask > ul > li{
    width: 100%;
    height: 40px;
    margin-top: 20px;
}
.f_ask > ul > li > button{
    float: left;
    width: 120px;
    height: 40px;
    font-size: 14px;
    color: #333;
    background: #fff;
    border: 1px solid #ccc;
    border-radius: 4px;
}
.f_ask > ul > li > p{
    float: left;
    color: #999;
    display: block;
    font-size: 14px;
    width: calc(100%-160px);
    margin-left: 20px;
    height: 40px;
}

.f_infor{
    width: 45%;
    height: 350px;
    float: right;
}
.f_infor .f_nav{
    margin: 0px auto;
    padding-bottom: 20px;
    font-size: 14px;
    display: flex;
    color: #333;
    justify-content: space-evenly;
}
.f_infor .f_nav > li:nth-child(5){
    font-weight: bold;
}
.f_infor > p{
    font-size: 12px;
    color: #999;
}
.f_infor > p > span{
    color: rgb(95, 0, 128);
}
.f_infor .f_sercle{
    margin: 0px auto;
    padding-top: 50px;
    font-size: 14px;
    display: flex;
    color: #333;
    justify-content: space-evenly;
}
.f_infor .f_sercle > li{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #ccc;
}

.f_copy{
    width: 100%;
    height: 50px;
    line-height: 50px;
    color: #999;
    font-size: 10px;
    text-align: center;
    border-top: 1px solid #333;
    text-transform: uppercase;
    font-style: normal;
}

.NotFound404{
    text-align: center;
}
.NotFound404 > h3{
    font-size: 36px;
    margin: 120px 0px 40px;
}
.NotFound404 > p > a > input{
    width: 160px;
    height: 40px;
    cursor: pointer;
    width: 160px;
    height: 40px;
    background: #fff;
    border: 1px solid #ccc;
    margin-bottom: 120px;
}